import { useState, useEffect, useCallback } from "react";
import { Grid, Menu, MenuItem } from "@material-ui/core";
import { SortableContainer, SortableElement } from "react-sortable-hoc";
import arrayMove from "array-move";
import BookmarksItem from "../BookmarksItem";
import BookmarksAddButton from "../BookmarksAddButton";
import bookmarksListStyles from "./styles";

const initialState = {
  id: "",
  anchorEl: null,
};

const SortableItem = SortableElement(
  ({
    bookmark,
    handleClick,
    setColorInState,
    options,
    classes,
    onOpenModal,
  }) => {
    return (
      <>
        <Grid item xs={12} sm={3} lg={options.col} className={classes.item}>
          <BookmarksItem
            key={bookmark.id}
            {...bookmark}
            onContextMenu={handleClick}
            setColorInState={setColorInState}
            options={options}
          />
        </Grid>
      </>
    );
  }
);

const SortableList = SortableContainer(
  ({
    bookmarks,
    options,
    handleClick,
    setColorInState,
    classes,
    onOpenModal,
  }) => {
    return (
      <Grid
        container
        spacing={options.spacing}
        className={bookmarks.length === 0 ? classes.grid : classes.gri}
        direction="row"
        justify="space-between"
        alignItems="center"
      >
        {bookmarks.map((bookmark, index) => {
          return (
            <SortableItem
              index={index}
              key={bookmark.id}
              bookmark={bookmark}
              handleClick={handleClick}
              setColorInState={setColorInState}
              options={options}
              classes={classes}
              onOpenModal={onOpenModal}
            />
          );
        })}
        <Grid item xs={12} sm={3} lg={options.col} className={classes.item}>
          <BookmarksAddButton handleOpenModal={onOpenModal} />
        </Grid>
      </Grid>
    );
  }
);

export default function BookmarksList({
  bookmarks,
  setBookmarks,
  onOpenModal,
  hhandleDelete,
  options,
  setColorInState,
}) {
  const classes = bookmarksListStyles();
  const [anchorEl, setAnchorEl] = useState(initialState);

  const handleClick = (event, id) => {
    event.preventDefault();
    setAnchorEl({ id: id, anchorEl: event.currentTarget });
  };

  const handleDelete = useCallback(
    (id) => {
      hhandleDelete(id);
      setAnchorEl(initialState);
    },
    [hhandleDelete]
  );

  const handleClose = () => {
    setAnchorEl(initialState);
  };
  useEffect(() => {
    console.log("RENDER LIST");
  }, []);

  const onSortEnd = ({ oldIndex, newIndex }) => {
    setBookmarks(arrayMove(bookmarks, oldIndex, newIndex));
  };
  return (
    <>
      <Grid
        container
        spacing={options.spacing}
        className={bookmarks.length === 0 ? classes.grid : classes.gri}
        direction="row"
        justify="space-between"
        alignItems="center"
      >
        <SortableList
          bookmarks={bookmarks}
          handleClick={handleClick}
          setColorInState={setColorInState}
          options={options}
          onSortEnd={onSortEnd}
          axis="xy"
          classes={classes}
          onOpenModal={onOpenModal}
          distance={1}
        />
        <Menu
          id="simple-menu"
          anchorEl={anchorEl.anchorEl}
          keepMounted
          open={Boolean(anchorEl.anchorEl)}
          onClose={handleClose}
        >
          <MenuItem onClick={() => handleDelete(anchorEl.id)}>Удалить</MenuItem>
        </Menu>
      </Grid>
    </>
  );
}
