import React from "react";
import { useState, useEffect } from "react";
import {
  Dialog,
  DialogActions,
  DialogContent,
  TextField,
  Button,
} from "@material-ui/core";

import DialogTitle from "@material-ui/core/DialogTitle";
export default function BookmarksAddDialog({ open, onClose, onSubmit }) {
  const initialValues = { url: "", title: "" };
  const [values, setValues] = useState(initialValues);
  const handleChange = (e) => {
    const name = e.target.name;
    return setValues({
      ...values,
      [name]: e.target.value,
    });
  };

  const handleBlur = () => {
    return setValues({
      ...values,
      title: values.url.replace(/(^\w+:|^)\/\//, "").split(".")[0],
    });
  };

  useEffect(() => {
    setValues(initialValues);
  }, [onClose]);

  return (
    <Dialog
      open={open}
      fullWidth={"fullWidth"}
      maxWidth={"xs"}
      onClose={onClose}
      aria-labelledby="form-dialog-title"
    >
      <DialogTitle id="form-dialog-title">Добавление сайта</DialogTitle>
      <DialogContent>
        <TextField
          onChange={(e) => handleChange(e)}
          name="url"
          value={values.url}
          autoFocus
          margin="dense"
          id="url"
          label="Адрес"
          type="text"
          fullWidth
          onBlur={handleBlur}
        />
        <TextField
          onChange={(e) => handleChange(e)}
          name="title"
          value={values.title}
          margin="dense"
          id="name"
          label="Название"
          type="text"
          fullWidth
        />
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose} color="primary">
          Отмена
        </Button>
        <Button onClick={() => onSubmit(values)} color="primary">
          Готово
        </Button>
      </DialogActions>
    </Dialog>
  );
}
