import { useState, useCallback } from "react";
import {
  Grid,
  Drawer,
  IconButton,
  Typography,
  Slider,
  Box,
  Switch,
  FormControlLabel,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  TextField,
} from "@material-ui/core";

import SettingsIcon from "@material-ui/icons/Settings";
import { imageToDataURL } from "../../halpers";

import settingsStyles from "./styles";

export default function Settings({ options, setOptions }) {
  const [state, setState] = useState(false);

  const classes = settingsStyles();

  const toggleDrawerStatus = () => {
    setState(true);
  };
  const closeDrawer = () => {
    setState(false);
  };

  const handleSettingsCol = (e, val) => {
    return setOptions({
      ...options,
      col: val,
    });
  };

  const handleSettingsSpacing = (e, val) => {
    return setOptions({
      ...options,
      spacing: val,
    });
  };

  const handleWidthContainer = (e, val) => {
    return setOptions({
      ...options,
      widthContainer: val,
    });
  };

  const handleSettingsSearch = (e) => {
    return setOptions({
      ...options,
      isSearch: e.target.checked,
    });
  };

  const handleToggleTheme = (e) => {
    return setOptions({
      ...options,
      isDarkTheme: e.target.checked,
    });
  };
  const handleBrandColor = (e) => {
    return setOptions({
      ...options,
      brandColor: e.target.checked,
    });
  };

  const handleSearchType = (e) => {
    return setOptions({
      ...options,
      searchType: e.target.value,
    });
  };

  const handleSetBackground = (e) => {
    return setOptions({
      ...options,
      background: e.target.value,
    });
  };

  return (
    <>
      <IconButton className={classes.iconBtn} onClick={toggleDrawerStatus}>
        {!state ? <SettingsIcon /> : null}
      </IconButton>
      <Drawer
        className={classes.drawer}
        variant="temporary"
        open={state}
        onClose={closeDrawer}
        anchor={"right"}
      >
        <Box className={classes.drawer}>
          <Typography id="discrete-slider" gutterBottom>
            Bookmark size
          </Typography>
          <Slider
            defaultValue={options.col}
            aria-labelledby="discrete-slider"
            valueLabelDisplay="auto"
            step={1}
            marks
            min={options.widthContainer === 1 ? 2 : 1}
            max={4}
            onChangeCommitted={handleSettingsCol}
          />
        </Box>
        <Box className={classes.drawer}>
          <Typography id="discrete-slider" gutterBottom>
            Spacing between bookmarks
          </Typography>
          <Slider
            defaultValue={options.spacing}
            aria-labelledby="discrete-slider"
            valueLabelDisplay="default"
            step={1}
            marks
            min={1}
            max={4}
            onChangeCommitted={handleSettingsSpacing}
          />
        </Box>
        <Box className={classes.drawer}>
          <Typography id="discrete-slider" gutterBottom>
            Size container
          </Typography>
          <Slider
            defaultValue={options.widthContainer}
            aria-labelledby="discrete-slider"
            valueLabelDisplay="default"
            step={1}
            marks
            min={1}
            max={3}
            onChangeCommitted={handleWidthContainer}
          />
        </Box>

        <Box className={classes.drawer}>
          <FormControlLabel
            control={
              <Switch
                checked={options.isSearch}
                onChange={handleSettingsSearch}
                color="primary"
              />
            }
            label="Search form"
          />
        </Box>
        {options.isSearch ? (
          <Box className={classes.drawer}>
            <FormControl className={classes.formControl} fullWidth={true}>
              <InputLabel id="demo-simple-select-label">
                Search system
              </InputLabel>
              <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                value={options.searchType}
                onChange={handleSearchType}
              >
                <MenuItem value={"google"}>Google</MenuItem>
                <MenuItem value={"yandex"}>Yandex</MenuItem>
                <MenuItem value={"duckduckgo"}>DuckDuckGo</MenuItem>
                <MenuItem value={"bing"}>Bing</MenuItem>
              </Select>
            </FormControl>
          </Box>
        ) : null}

        <Box className={classes.drawer}>
          <FormControlLabel
            control={
              <Switch
                checked={options.isDarkTheme}
                onChange={handleToggleTheme}
                color="primary"
              />
            }
            label="Dark mode"
          />
        </Box>
        <Box className={classes.drawer}>
          <FormControlLabel
            control={
              <Switch
                checked={options.brandColor}
                onChange={handleBrandColor}
                color="primary"
              />
            }
            label="Auto brand color"
          />
        </Box>

        <Box className={classes.drawer}>
          <Typography gutterBottom>Background image</Typography>
          <form>
            <TextField
              fullWidth={true}
              placeholder="url image"
              value={options.background}
              onChange={handleSetBackground}
            />
          </form>
        </Box>
        <Box className={classes.drawer}>
          <Typography gutterBottom>Background image</Typography>
          <Grid
            container
            spacing={options.spacing}
            direction="row"
            justify="space-between"
            alignItems="center"
          >
            <div className={classes.shapeCircle} sm={3} />
            <div className={classes.shapeCircle} sm={3} />
            <div className={classes.shapeCircle} sm={3} />
            <div className={classes.shapeCircle} sm={3} />
          </Grid>
        </Box>
      </Drawer>
    </>
  );
}
