import { memo, useEffect, useState } from "react";
import useImageLoaded from "../../../hooks/useImageLoaded";
import {
  Card,
  CardActionArea,
  CardContent,
  Typography,
  CircularProgress,
} from "@material-ui/core";
import bookmarksItemStyles from "./styles";

const BookmarksItem = ({
  id,
  title,
  url,
  icon,
  brandColor,
  onContextMenu,
  setColorInState,
  options,
}) => {
  const classes = bookmarksItemStyles();
  const [ref, loadedIcon, onLoad] = useImageLoaded();
  const [loadedBackground, setLoadedBackground] = useState(brandColor);
  const [color, setColor] = useState(brandColor);

  useEffect(() => {
    console.log("RENDER ITEM");
    if (brandColor) {
      console.log("Есть");
    } else {
      fetch(`https://brand-color.herokuapp.com/api/getColor?url=${url}`)
        .then((response) => response.json())
        .then(({ color1 }) => {
          setLoadedBackground(true);
          return color1;
        })
        .then((color) => {
          setColor(color);
        })
        .catch((err) => {
          console.log(err);
        });
    }
  }, []);
  return (
    <Card
      className={classes.root}
      style={options.brandColor ? { background: color } : null}
    >
      <CardActionArea
        href={url}
        className={classes.action}
        onContextMenu={(e) => onContextMenu(e, id)}
      >
        <CardContent className={classes.content}>
          {loadedBackground ? null : (
            <CircularProgress className={classes.progressBg} size={15} />
          )}
          <img
            style={{
              display: loadedIcon ? "block" : "none",
            }}
            className={classes.media}
            ref={ref}
            onLoad={onLoad}
            src={icon}
            alt=""
          />
          {loadedIcon ? null : <CircularProgress size={30} />}

          <Typography
            className={classes.title}
            color="textSecondary"
            gutterBottom
          >
            {title}
          </Typography>
        </CardContent>
      </CardActionArea>
    </Card>
  );
};
export default memo(BookmarksItem);
