import { useState, useEffect } from "react";
import {
  createMuiTheme,
  MuiThemeProvider,
  CssBaseline,
  Box,
  Container,
} from "@material-ui/core";
import useDidMountEffect from "../../hooks/useDidMountEffect";
import { useLocalStorage } from "../../hooks/useLocalStorage";
import { lightBlue, deepOrange } from "@material-ui/core/colors";
import Bookmarks from "../Bookmarks";
import SearchForm from "../SearchForm";
import Settings from "../Settings";
import appStyles from "./styles.js";
const themeObject = {
  palette: {
    type: "light",
    primary: lightBlue,
    secondary: deepOrange,
  },
};
const light = {
  palette: {
    type: "light",
    primary: lightBlue,
    secondary: deepOrange,
  },
};

const dark = {
  palette: {
    type: "dark",
    primary: lightBlue,
    secondary: deepOrange,
  },
};

const initialOptions = {
  col: 2,
  spacing: 1,
  isSearch: true,
  searchType: "google",
  widthContainer: 2,
  isDarkTheme: true,
  background: "",
  colorBrand: true,
};

function App() {
  const [theme, setTheme] = useState(true);
  const classes = appStyles();
  const [storedOptions, setStoredOptions] = useLocalStorage("options");
  const [options, setOptions] = useState(
    storedOptions ? storedOptions : initialOptions
  );

  useDidMountEffect(() => {
    setStoredOptions(options);
  }, [options]);

  useDidMountEffect(() => {
    setTheme(options.isDarkTheme);
    setStoredOptions(options.isDarkTheme);
  }, [options.isDarkTheme]);

  useEffect(() => {
    console.log("RENDER APP");
  }, []);

  const muiTheme = createMuiTheme(theme ? dark : light);
  const widthContainer = ["sm", "md", "lg"];
  return (
    <>
      <MuiThemeProvider theme={muiTheme}>
        <CssBaseline />
        <Box
          className={classes.box}
          style={{
            background: `url(${options.background}) center center / cover no-repeat`,
          }}
        >
          <Container
            maxWidth={
              options.widthContainer === 1
                ? widthContainer[0]
                : widthContainer[options.widthContainer - 1]
            }
          >
            {options.isSearch ? <SearchForm type={options.searchType} /> : null}
            <Bookmarks options={options} />
          </Container>
        </Box>
        <Settings options={options} setOptions={setOptions} />
      </MuiThemeProvider>
    </>
  );
}

export default App;
