import { useState, useEffect, useCallback } from "react";
import { v4 } from "uuid";
import BookmarksList from "./BookmarksList";
import BookmarksAddDialog from "./BookmarksAddDialog";
import { Box } from "@material-ui/core";
import { useLocalStorage } from "../../hooks/useLocalStorage";
import { withHttp, cleanUrl } from "../../halpers";

export default function Bookmarks({ options }) {
  const [dialogIsOpen, setDialogIsOpen] = useState(false);
  const [bookmarks, setBookmarks] = useState([]);
  const [storedBookmarks, setStoredBookmarks] = useLocalStorage("bookmarks");

  const openDialog = () => setDialogIsOpen(true);

  const closeDialog = () => setDialogIsOpen(false);

  const setColorInState = (id, color) => {
    const modifyBookmarkColor = bookmarks.map((bookmark) =>
      bookmark.id === id ? { ...bookmark, brandColor: color } : bookmark
    );
    setStoredBookmarks(modifyBookmarkColor);
    return setBookmarks(modifyBookmarkColor);
  };

  const addBookmark = ({ title, url }) => {
    const updatesBookmarks = [
      ...bookmarks,
      {
        id: v4(),
        title: title.toUpperCase(),
        url: withHttp(url),
        icon: `https://api.faviconkit.com/${cleanUrl(url)}/20`,
        brandColor: "",
      },
    ];
    console.log(updatesBookmarks);
    setBookmarks(updatesBookmarks);
    setStoredBookmarks(updatesBookmarks);
    closeDialog();
  };

  useEffect(() => {
    // console.log(bookmarks);
  }, [bookmarks]);

  const deleteBookmark = (id) => {
    const updatesBookmarks = bookmarks.filter((el) => el.id !== id);
    setBookmarks(updatesBookmarks);
    setStoredBookmarks(updatesBookmarks);
  };

  useEffect(() => {
    const bookmarksStorage = localStorage.getItem("bookmarks");
    if (bookmarksStorage) setBookmarks(JSON.parse(bookmarksStorage));
  }, []);

  useEffect(() => {
    console.log("RENDERING BOOKMARKS");
  }, []);
  return (
    <>
      <Box>
        <BookmarksList
          bookmarks={bookmarks}
          setBookmarks={setBookmarks}
          onOpenModal={openDialog}
          hhandleDelete={deleteBookmark}
          options={options}
          setColorInState={setColorInState}
        />
        <BookmarksAddDialog
          open={dialogIsOpen}
          onSubmit={addBookmark}
          onClose={closeDialog}
          count={bookmarks.length}
        />
      </Box>
    </>
  );
}
